.App {
    text-align: center;
}
.App-header {
    position: fixed;
    background-color: #54745b;
    width: 100vw;
    min-height: 70px;
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: center;
    z-index: 1000px;
}

.contents-area {
    width: 1200px;
    margin: 0 auto;
    display: flex;
}

.spacer {
    height: 70px;
}

.main-carousel {
    height: 500px;
    background-color: #55ca92;
}

.carousel-font {
    margin-top: 200px;
    font-family: "Noto Sans KR";
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 35px;

    color: #000000;
}

.dream-book-description {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.description-bold {
    font-family: "Noto Sans KR";
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 70px;

    color: #000000;
}

.description-normal {
    font-family: "Noto Sans KR";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 0px;

    color: rgba(28, 31, 41, 0.8);
}

.card-list-div {
    margin-top: 80px;
    margin-bottom: 100px;
    display: flex;
    justify-content: space-between;
}

.card-item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    width: 260px;
    height: 150px;
    background: #fafafa;
    border-radius: 16px;
}

.card-bold {
    font-family: "Noto Sans KR";
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    margin-bottom: 20px;
    margin-left: 30px;
    color: #000000;
}

.card-normal {
    margin-left: 30px;
    font-family: "Noto Sans KR";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.5;

    color: #000000;
}

.instagram-banner {
    height: 80px;

    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.instagram-banner-background {
    background: linear-gradient(270deg, #f4e2d8 29.64%, #ba5370 67.88%);
}

.instagram-banner-bold {
    font-family: "Noto Sans KR";
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 35px;
    margin-right: 24px;

    color: #000000;
}

.contents-image1 {
    margin-top: 190px;
    margin-right: 17px;
    height: 400px;
}

.contents-image2 {
    height: 500px;
    left: 561px;
    top: -77px;
}
